}

.experience-card em {
  color: var(--color-primary);
  margin: .25rem 0 .625rem;
  font-weight: 500;
  display: block;
}

.experience-details {
  text-align: left;
  margin: 0;
  padding: 0;
  list-style: inside;
}

.experience-details li {
  margin-bottom: .375rem;
